/*--------------------adjustment----------------------*/
* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  font-family: "IBMPlexSans-Regular", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  min-width: 320px;
  position: relative;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  color: #2D2D2F;
}

ul, li {
  margin: 0;
  padding: 0;
}

input input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  appearance: none;
}

.input,
input {
  outline: none;
  display: block;
  width: 100%;
}

textarea.input {
  resize: none;
  padding: 0 0;
}

button {
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0;
}

.main {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.main .header, .main .footer {
  flex: 0 0 auto;
}

.main .main-content {
  flex: 1 1 auto;
}

.wrapper {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 1200px;
}

.wrapper-simple {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
